/**
 * HealthTrendsHeader 
 *         
 */
@use '../../../styles/variables' as *;
@use '../../../styles/mixins' as *;

.health-trends-header {
  //        
  position: fixed !important; //    (  )
  top: 0 !important; //  
  left: 0 !important; //   (  )
  right: 0 !important; //  (  )
  width: 100vw !important; //    (  )
  max-width: 100vw !important; //    
  min-width: 100vw !important; //   100vw
  box-sizing: border-box !important;
  z-index: 100 !important; //    
  padding: 0 !important; //    
  margin: 0 !important; //   
  margin-left: calc(-50vw + 50%) !important; //     
  margin-right: calc(-50vw + 50%) !important; //     
  transform: none !important; // transform  (left/right  )
  
  //      (  )
  background: #FEF9EE !important; //  
  border-radius: 0 0 24px 24px !important; //   (   , 12px -> 24px)
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08) !important; //    
  height: 90px !important; //  (60px) 150% = 90px
  display: flex !important;
  align-items: center !important; //   
  padding-left: $spacing-md !important; //     
  padding-right: $spacing-md !important; //     
  position: relative !important; //   

  //   (  )
  &__header-greeting-section {
    width: 100% !important; //    
    height: 100% !important; //    
    margin: 0 !important;
    padding: 0 !important; //   ( )
    position: relative !important;

    //   () -   
    .back-button {
      position: absolute !important; //  
      left: $spacing-md !important; //   
      top: 50% !important; //  
      transform: translateY(-50%) !important; //   
      z-index: 1 !important; //   
    }
  }

  //   (     )
  &__center {
    width: 100% !important; //    
    display: flex;
    flex-direction: column;
    align-items: center; // X   (   )
    justify-content: center; // Y  
    gap: $spacing-xs; //    
    position: absolute !important; //       
    left: 0 !important;
    right: 0 !important;
    top: 50% !important;
    transform: translateY(-50%) !important; //   
    z-index: 0 !important; //  
  }

  //  -    ,   ,  
  &__title {
    text-align: center; //  
    font-size: 18px !important; //   (20px -> 18px)
    font-weight: $font-weight-normal !important; //   
    color: $black !important;
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif) !important; //  
    margin: 0 !important;
    line-height: 1.2 !important;
  }

  //    ( ,  )
  &__update {
    display: flex;
    align-items: center;
    justify-content: center; //  
    gap: 6px;
    font-size: $font-xs; // 12px
    color: $gray-550;
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
    
    &-icon {
      font-size: $font-xs;
      color: $gray-550;
    }
    
    &-text {
      font-size: $font-xs;
      color: $gray-550;
    }
    
  }
  
  //   (   )
  &__refresh-icon {
    width: 18px;
    height: 18px;
    border: none;
    background: transparent;
    cursor: pointer;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-shrink: 0;
    transition: all 0.2s ease;
    
    &:hover {
      transform: scale(1.1) rotate(90deg);
    }
    
    &:active {
      transform: scale(0.95) rotate(90deg);
    }
    
    // SVG  
    &-svg {
      width: 14px;
      height: 14px;
      transition: transform 0.3s ease;
    }
    
    &:hover &-svg {
      transform: rotate(180deg);
    }
  }

  //  
  @media (max-width: 767px) {
    padding: $spacing-sm $spacing-sm !important;
    min-height: 52px;
    gap: $spacing-sm;
    
    &__back-button {
      width: 36px;
      height: 36px;
      
      svg {
        width: 18px;
        height: 18px;
      }
    }
    
    &__title {
      font-size: $font-base; // 16px
    }
    
    &__update {
      font-size: 11px;
      
      &-icon {
        font-size: 11px;
      }
      
      &-text {
        font-size: 11px;
      }
    }
  }
}

