/**
 * VisitTrendsChart 
 */
@use '../../../styles/variables' as *;

.visit-trends-wrapper {
  // HealthDataViewer    
  .visit-trends-header {
    margin-bottom: 2px !important; //   
    text-align: left;
    
    .visit-trends-title {
      font-size: 14px !important;
      font-weight: $font-weight-bold;
      color: #A16A51 !important;
      margin: 0 !important;
      font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
    }
  }
  
  .visit-trends-container {
    margin-top: $spacing-sm !important; //     (-8px  $spacing-sm)
    padding-top: 0 !important;
    padding-left: 0 !important; //      
    padding-right: 0 !important; //      
    display: flex;
    justify-content: center; //   
  }
  
  //      (HealthDataViewer  )
  .visit-trend-card {
    padding: $spacing-sm $spacing-sm 8px $spacing-sm !important; //    ($spacing-md  $spacing-sm)
    flex-shrink: 0; //   
    width: 100%; //   
    max-width: 100%; //   
    
    &.visit-trend-card--combined {
      //   
    }
    
    .trend-header {
      margin-bottom: 6px !important; //       ( 16px 6px)
      
      .trend-title {
        margin-bottom: 0 !important; //   
      }
    }
    
    .trend-chart {
      margin-top: 0 !important; //   
      padding-top: 0 !important; //   
      padding-left: 0 !important; //   
      padding-right: 0 !important; //   
      margin-bottom: 0 !important; //    (  )
      padding-bottom: 0 !important; //   
      display: flex;
      flex-direction: column;
      align-items: stretch; //    (center  stretch)
      width: 100%; //   
    }
    
    //  
    .visit-trends-legend {
      display: flex;
      justify-content: center;
      gap: $spacing-md;
      margin-top: 4px !important; //    
      padding-top: 0 !important;
      
      .legend-item {
        display: flex;
        align-items: center;
        gap: $spacing-xs;
        
        .legend-color {
          width: 12px;
          height: 12px;
          border-radius: 2px;
        }
        
        .legend-label {
          font-size: 12px;
          color: $gray-700;
          font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
        }
      }
    }
  }
  
  //    
  .visit-history-header {
    margin-top: $spacing-md !important;
    margin-bottom: $spacing-sm !important;
    
    .visit-history-title {
      font-size: 14px !important;
      font-weight: $font-weight-bold;
      color: #A16A51 !important;
      margin: 0 !important;
      font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
    }
  }
  
  // BarChart    -    
  .wello-bar-chart {
    width: 100% !important; //   
    max-width: 100% !important; //   
    //   
    margin-top: -10px !important; //     ()
    padding-top: 0 !important;
    margin-left: 0 !important; //   
    margin-right: 0 !important; //   
    margin-bottom: -30px !important; //     
    padding-bottom: 0 !important;
    
    // SVG   
    &__svg {
      width: 100% !important; //   
      max-width: 100% !important; //   
      margin-top: -10px !important; // SVG    ()
      padding-top: 0 !important;
      margin-left: 0 !important; //   
      margin-right: 0 !important; //   
      margin-bottom: -30px !important; // SVG    
      padding-bottom: 0 !important;
      transform: translateY(-5px) !important; //   ()
    }
    
    //    (   )
    &__grid {
      line.wello-bar-chart__grid-line {
        stroke: #E0E0E0 !important; //   
        stroke-width: 0.5 !important; //  
        stroke-dasharray: 2, 4 !important; //   
        opacity: 0.4 !important; //  
      }
    }
  }
  
  //      ( ID   )
  .visit-trend-card--combined {
    //  
    .wello-bar-chart__bar[data-series-id="pharmacy-visit-trend"],
    [data-series-id="pharmacy-visit-trend"] .wello-bar-chart__bar {
      fill: #FFEAE2 !important;
      stroke: #FFEAE2 !important;
    }
    
    //    -    
    .wello-bar-chart__value-label[data-series-id="pharmacy-visit-trend"],
    [data-series-id="pharmacy-visit-trend"] .wello-bar-chart__value-label,
    g[data-series-id="pharmacy-visit-trend"] .wello-bar-chart__value-label {
      fill: #FE6F32 !important; //     
      opacity: 1 !important;
      visibility: visible !important;
      display: block !important;
    }
    
    //  
    .wello-bar-chart__bar[data-series-id="hospital-visit-trend"],
    [data-series-id="hospital-visit-trend"] .wello-bar-chart__bar {
      fill: #DDF2FF !important;
      stroke: #DDF2FF !important;
    }
    
    //    -    
    .wello-bar-chart__value-label[data-series-id="hospital-visit-trend"],
    [data-series-id="hospital-visit-trend"] .wello-bar-chart__value-label,
    g[data-series-id="hospital-visit-trend"] .wello-bar-chart__value-label {
      fill: #327FEB !important; //     
      opacity: 1 !important;
      visibility: visible !important;
      display: block !important;
    }
  }
  
  // BarChart   ()
  .wello-bar-chart {
    
    //    
    &__x-axis,
    &__y-axis {
      .wello-bar-chart__axis-label {
        fill: #626262 !important; //  
        font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, system-ui, sans-serif !important;
        font-size: 12px !important;
      }
      
      .wello-bar-chart__axis-tick {
        stroke: #E0E0E0 !important;
        stroke-width: 0.5 !important;
      }
      
      .wello-bar-chart__axis-text {
        fill: #626262 !important; //  
        font-family: 'Poppins', -apple-system, BlinkMacSystemFont, system-ui, sans-serif !important;
        font-size: 8px !important;
        font-weight: 400 !important; // Regular
      }
    }
    
    //    (Pretendard Bold 12px)
    &__value-label {
      font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, system-ui, sans-serif !important;
      font-size: 12px !important;
      font-weight: $font-weight-bold !important;
      opacity: 1 !important;
      visibility: visible !important;
      display: block !important;
      fill: #626262 !important; //   ( )
      pointer-events: none !important; //   
    }
  }
}

